<template>
  <div id="app">
    <header>
      <h1 class="title">8 марта</h1>
    </header>
    <img v-if="item < 0" @click="open()" class="surprise" src="./assets/surprise_closed.png"/>
    <img v-else class="surprise" src="./assets/surprise_opened.png"/>
    <h4 class="text-opened" v-if="item < 0">Открой коробку и получи подарок на <b style="color: red">8 марта</b></h4>
    <h4 class="text-opened" v-else>Коробка уже открыта</h4>
    <div v-if="takeState" :class="['got-surprise-wrapper', {'taken': takeState === 1, 'gifts': takeState === 2}]">
      <div class="surprise-wrapper">
        <div class="count-wrapper">x{{ count }}</div>
        <div class="burn-wrapper">
          <img class="burn" :src="'./img/' + gifts[item].img" />
        </div>
        <h4 class="gift-description">{{ gifts[item].name }}</h4>
        <button @click="take()" class="take">Закрыть</button>
        <h5 class="gift-screen">Сделай скрин и отправь мне в личку</h5>
      </div>
    </div>
    <button v-if="item < 0" @click="open()" class="open">Открыть</button>
    <button v-else @click="openGifts()" class="my-gifts">Просмотреть мой подарок</button>
  </div>
</template>

<script>
export default {
  name: 'App',
  data: () => {
    return {
      opened: false,
      item: localStorage.getItem('item') || -1,
      count: localStorage.getItem('count') || -1,
      takeState: 0,
      gifts: [
        {
          name: 'Энергетик берн зелененький',
          img: 'burn.png'
        },
        {
          name: 'Цветочки красивые',
          img: 'flowers.jpg'
        },
        {
          name: 'Пак сладкоежки',
          img: 'sweets.jpg'
        },
        {
          name: 'Стикерпак',
          img: 'stickers.jpg'
        }
      ]
    }
  },
  methods: {
    openGifts: function () {
      this.takeState = 2
    },
    take: function () {
      this.takeState = 1
    },
    open: function () {
      this.opened = true
      this.takeState = 2
      this.item = Math.floor(Math.random() * this.gifts.length)
      this.count = Math.floor((Math.random() * 2) + 1)
      localStorage.setItem('item', this.item)
      localStorage.setItem('count', this.count)
    }
  }
}
</script>

